@import url(https://fonts.googleapis.com/css2?family=Changa+One&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(90deg, #222 21px, transparent 1%) center,linear-gradient(#222 21px, transparent 1%) center,#444;background-size:22px 22px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.tip{opacity:0.7;font-size:13px;padding:20px;padding-top:0px;color:white}.button-positive{padding:20px;background-color:#1cc96a;border-radius:5px;color:white;transition:0.2s;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.button-positive:hover{background-color:#1eb462}.button-negative{padding:20px;background-color:#b63232;border-radius:5px;color:white;transition:0.2s;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.button-negative:hover{background-color:#a12c2c}select{padding:15px;border:2px solid #4b4b4b;background-color:#222;color:white;font-family:"Changa One", cursive;border-radius:5px;margin:2px}input{padding:15px;border:2px solid #4b4b4b;background-color:#222;color:white;font-family:"Changa One", cursive;border-radius:5px;margin:2px}input::-webkit-input-placeholder{color:rgba(255,255,255,0.685)}input:-ms-input-placeholder{color:rgba(255,255,255,0.685)}input::-ms-input-placeholder{color:rgba(255,255,255,0.685)}input::placeholder{color:rgba(255,255,255,0.685)}.button{padding:15px;border:2px solid #9b4a4a;background-color:#703030;color:white;font-family:"Changa One", cursive;border-radius:5px;margin:2px;cursor:pointer;transition:0.2s}.button:hover{background-color:#5e2626}::-webkit-scrollbar{width:8px}::-webkit-scrollbar-track{background:rgba(46,46,46,0.412)}::-webkit-scrollbar-thumb{background:#703030}::-webkit-scrollbar-thumb:hover{background:#9b4a4a}.swal2-title{font-family:"Changa One", cursive;font-weight:normal !important}.swal2-styled.swal2-confirm{font-family:"Changa One", cursive;background-color:#703030 !important}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.filter-container{display:flex;align-items:center;background-color:#252525;padding:15px;margin-bottom:15px}@media only screen and (max-width: 600px){.filter-container{flex-direction:column}select{width:100%}input{width:91%}}

.carta-container{width:200px;font-family:"Changa One", cursive;margin:5px;text-align:center}.carta-container .carta{display:flex;flex-direction:column;height:100%}.carta-container .carta .image{background-color:#eee8d4;border-radius:5px;margin-bottom:5px}.carta-container .carta .image img{width:auto;height:200px}.carta-container .carta .extra{background-color:#dad2bb;border-radius:5px;padding:10px;margin-bottom:5px}.carta-container .carta .ataque{background-color:#703030;color:white;border-radius:5px;padding:10px;margin-bottom:5px}.carta-container .carta .defesa{background-color:#e0a65a;color:white;border-radius:5px;padding:10px;margin-bottom:5px}.carta-container .carta .content{background-color:#eee8d4;border-radius:5px;padding:20px;padding-bottom:5px;padding-top:5px;height:100%;margin-bottom:5px}.carta-container .carta .content .nome{font-size:25px}

.lista-container{display:flex;overflow:auto}.lista-container .lista{display:flex;justify-content:center;flex-wrap:nowrap;align-items:stretch;margin-left:20px}


.home-container .title{font-size:50px;font-family:"Changa One", cursive;color:white;padding:15px}.home-container .loading{color:white;padding:20px}

.inicio-container{display:flex;flex-direction:column;align-items:center;margin-top:10%}.inicio-container select{width:100%;font-size:18px;text-align:center}.inicio-container .box .title{font-size:50px;font-family:"Changa One", cursive;color:white}.inicio-container .box .button{text-align:center}

